.app {
  height: 100vh; 

  .sections {
    width: 100%;
    height: calc(100vh - 70px);
    background-color: rgb(40, 102, 110);
    position: relative;
    top: 70px;
    scroll-behavior: smooth; 
    scroll-snap-type: y mandatory;
    scrollbar-width: none; //for firefox
    &::-webkit-scrollbar { //for anyother
      display: none;
    }
    > *{
      width: 100vw;
      height: calc(100vh - 70px);
      scroll-snap-align: start;
    }
  }
}

